@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:400,700);
#root {
  height: 100vh;
}


body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-family: "Nunito Sans", sans-serif;
}



.App {
  height: 100%;
  display: flex;
  color: rgba(189, 189, 192, 1);
}



.Main {
    flex: 1 1;
    background-color: rgba(25, 25, 27, 1);
    height: 100%;
}


.Empty {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
}

.Empty__name {
    color: #fff;
}

.Empty__status, .Empty__info {
    padding: 1rem;
}

.Empty__status {
    color: rgba(255, 255, 255, 0.9);
    border-bottom: 1px solid rgba(255, 255, 255, 0.7);
}

.Empty__img {
    border-radius: 50%;
    margin: 2rem 0;
}

.Empty__btn {
    padding: 1rem;
    margin: 1rem 0;
    font-weight: bold;
    font-size: 1.2rem;
    border-radius: 30px;
    outline: 0;
}

.Empty__btn:hover {
    background: rgba(255, 255, 255, 0.7);
    cursor: pointer;
}
.ChatWindow {
    display: flex;
    flex-direction: column;
    height: 100vh;
}


.Header {
    padding: 1rem 2rem;
    border-bottom: 1px solid rgba(249, 249, 253, 0.2);
    display: inline-block;
}

.Header__details {
    display: flex;
}

.Header__name {
    margin: auto .5rem;
    color: #fff;
}

.Header__image {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    padding: 0.5rem 1rem;
}
.Header__status {
    margin-left: 1rem;
    color: #fff;
}

.Chats {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 85%;
    margin: 0 auto;
    overflow-y: scroll;
}

.Chat {
    margin: 1rem 0;
    color: #000;
    padding: 1rem;
    background: -webkit-gradient(linear, left top, right top, from(#1986d8), to(#7b9cc2));
    background: -webkit-linear-gradient(left, #1986d8, #7b9cc2);
    background: linear-gradient(90deg, #1986d8, #7b9cc2);
    max-width: 90%;
    border-radius: 0 10px 10px 0;
}

.Chat.is-user-msg {
    margin-left: auto;
    background: #e8e9f0;
    border-radius: 10px 0 0 10px;
    position: relative;
}

.Chat.is-user-msg:hover .close__btn {
    display: block;
}

.close__btn {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 17px;
    /* height: 30px; */
    display: none;
    color: #000;
    border: 1px solid #000;
    border-radius: 50%;
    text-align: center;
}

@media (min-width: 576px) {
    .Chat {
        max-width: 60%;
    }
}
.Message {
    width: 80%;
    margin: 1rem auto;
}

.Message__input {
    width: 100%;
    padding: 1rem;
    background: rgba(255, 255, 255, 0.8);
    color: #000;
    border: 0;
    border-radius: 10px;
    font-size: 1rem;
    outline: 0;
}
.User {
    display: flex;
    align-items: flex-start;
    padding: 1rem;
}

.User:hover {
    background: rgba(0, 0, 0, 0.2);
    cursor: pointer;
}

.User__pic {
    width: 50px;
    border-radius: 50%;
}

.User__details {
    display: none;
}



/* not small devices */
@media (min-width: 576px) {
    .User__details {
        display: block;
        padding: 0 0 0 1rem;
    }
    .User__details-name {
        margin: 0;
        color: rgba(255, 255, 255, 0.8);
        font-size: 1rem;
    }
}
.Sidebar {
    width: 80px;
    background-color:  rgba(32, 32, 35, 1);
    height: 100%;
    border-right: 1px solid rgba(189, 189, 192, 0.1);
    -webkit-transition: width 0.3s;
    transition: width 0.3s;
    overflow-y: scroll;
}

/* not small devices */

@media (min-width: 576px) {
    .Sidebar {
        width: 320px;
    }
}
